import Vue from 'vue'
import Vuex from 'vuex'

Vue.use(Vuex)

export default new Vuex.Store({
  state: {
    board: [
      { id: 1, title: "첫번째 제목", memo: "첫번째 작성된 메모입니다", views: 0 },
      { id: 2, title: "두번째", memo: "두번째 작성된 메모입니다", views: 0 }
    ]
  },
  getters: { // computed 여러번 활용하기 위해

  },
  mutations: { // eventhandler 역할
    addmemo(state, {title, memo} ) {
      // 메모값을 보드에 추가하는 함수
      // 메모값을 스토어에서 추가할 수 없기때문에 인수로 받아와야한다.
      const id = state.board.length + 1;
      state.board.push({ id: id, title: title, memo: memo, views: 0 });
    }
  },
  actions: {
  },
  modules: {
  }
})
